<template>
    <div ref="echartBar" class="echart-bar" id="echart-bar"></div>
</template>
<script>
// ECharts 
import echarts from "echarts/lib/echarts";
// 
import "echarts/lib/chart/bar";
// 
import "echarts/lib/component/tooltip";
// dataZoom
import "echarts/lib/component/dataZoom";
export default {
  props: {
    selfAverage: {
      type: Array,
      default: () => []
    },
    supAverage: {
      type: Array,
      default: () => []
    },
    departmentsAverage: {
      type: Array,
      default: () => []
    },
    yMin: {
      type: Number,
      default: 0
    },
    yMax: {
      type: Number,
      default: 0
    },
    yInterval: {
      type: Number,
      default: 1
    },
    width: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      myChart: null
    };
  },
  computed: {
    option: function() {
      return {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // cross
            type: "shadow",
            crossStyle: {
              color: "#999"
            },
            shadowStyle: {
              color: "transparent"
            }
          },
          position: "top",
          // formatter: function(datas) {
          //   return datas[0].value + "/" + datas[1].value;
          // },
          formatter: "{c0}/{c1}"
        },
        color: ["#3BDABC", "#72b7f5"],
        xAxis: [
          {
            type: "category",
            data: this.departmentsAverage,
            grid: {
              y2: 140
            },
            axisLabel: {
              // 
              // interval: 0,
              // -30
              rotate: -18,
              textStyle: {
                color: "#999"
              }
            },
            // x
            axisLine: { show: false },
            // x
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            min: this.yMin,
            max: this.yMax,
            interval: this.yInterval,
            // Y
            show: true,
            // 
            splitLine: {
              show: true,
              // 
              lineStyle: {
                // 
                color: ["#eee"]
              }
            },
            axisLabel: {
              formatter: "{value}",
              textStyle: {
                color: "#c7c7c7"
              }
            },
            // y
            axisLine: { show: false },
            // y
            axisTick: {
              show: false
            }
          }
        ],
        dataZoom: [
          {
            type: "slider"
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            data: this.selfAverage,
            barWidth: 12,
            itemStyle: {
              normal: {
                barBorderRadius: [6, 6, 0, 0],
                color: new echarts.graphic.LinearGradient(1, 0, 0, 1, [
                  { offset: 0, color: "#3BDABC" },
                  { offset: 1, color: "#3BDABC" }
                ])
              }
            }
          },
          {
            name: "",
            type: "bar",
            data: this.supAverage,
            barWidth: 12,
            itemStyle: {
              normal: {
                // 
                barBorderRadius: [6, 6, 0, 0],
                color: new echarts.graphic.LinearGradient(1, 0, 0, 1, [
                  { offset: 0, color: "#72b7f5" },
                  { offset: 1, color: "blue" }
                ])
              }
            }
          }
        ]
      };
    }
  },
  mounted() {
    this.myChart = echarts.init(this.$refs.echartBar);
    this.myChart.setOption(this.option);
  },
  watch: {
    selfAverage: function() {
      this.myChart.setOption(this.option);
    },
    supAverage: function() {
      this.myChart.setOption(this.option);
    },
    departmentsAverage: function() {
      this.myChart.setOption(this.option);
    },
    // 
    width: function() {
      const width = document.getElementById("echart-bar").clientWidth;
      this.myChart.resize({ width });
    }
  }
};
</script>

<style scoped>
.echart-bar {
  height: 100%;
  width: 100%;
  display: inline-block;
}
</style>



